.TodoListItem {
  padding: 1rem;
  display: flex;
  align-items: center;
  &:nth-child(even) {
    background: #f8f9fa;
  }

  .box {
    border: 1px solid #bcbcbc;
    margin-right: 2px;
    display: flex;
    align-items: center;
    flex: 1;

    .boxName {
      width: 50%;
      border-right: 1px solid #bcbcbc;
      margin-right: 2px;
      align-items: center;
      flex: 2;
    }
    .boxPrice {
      width: 30%;

      border-right: 1px solid #bcbcbc;
      margin-right: 2px;
      align-items: center;
      flex: 1;
    }
    .boxStock {
      width: 10%;
      margin-right: 2px;
      align-items: center;
      flex: 1;
    }
  }

  .checkbox {
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
    svg {
      font-size: 1.5rem;
    }
    .text {
      margin-left: 0.5rem;

      flex: 1;
    }
    &.checked {
      svg {
        color: #22b8cf;
      }
      .text {
        color: #adb5db;
        text-decoration: line-through;
      }
    }
  }
  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
    &:hover {
      color: #ff8787;
    }
  }

  & + & {
    border-top: 1px solid #dee2e6;
  }
}
